import StringUtils from "./StringUtils";
/**
 * Clase for utilities for color
 */
export default class ColorUtils {
  /**
   * Method for convert hex color to rgba
   * @param hex color
   * @param opacity for rgpa
   */
  static convertHexColorToRgba(hex, opacity) {
    let result = hex;
    if (StringUtils.isNotBlank(result)) {
      result = hex.replace("#", "");
      let r = parseInt(result.substring(0, 2), 16);
      let g = parseInt(result.substring(2, 4), 16);
      let b = parseInt(result.substring(4, 6), 16);

      result = "rgba(" + r + "," + g + "," + b + "," + opacity + ")";
    }
    return result;
  }
}
